<template>
    <div class="float-right displayinlineblock pt-2">
        <div id="mainDiv" class="date_range">
            <a :id="'aDateLable_' + pgaction" href="javascript:;"
                class="btn btn-info formbtn inline-block showDatePanel mr-2 py-1" 
                @click="OpenDatePopUp(pgaction)">
                <i class="fa fa-bars mt-1 pr-2"></i><em class="text-white ">{{ $t(FilterSpan) }}</em>
            </a>
            <div :id='"DivTimer_" + pgaction' :data-pageaction="pgaction" class="DatePopUp divtimer_design p-1"
                style="display: none">
                <table class="table-responsive AssetDatePopUp d-block ">
                    <tbody class="daterangebody">
                        <tr class="daterangetr" ref="daterange" style="display: table !important; width: 100%;">
                            <td class="sep daterangetd">
                                <ul class="date_select">
                                    <li id="1"><a :title="$t('Today')" id="aToday" typ="Today" class="Today"
                                            @click="ClickTimer('Today', this, pgaction);">{{ $t('Today') }}</a><i id="pin1"
                                            class="fa fa-thumb-tack mt-1 pinhide inpinner" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                    <li id="2"><a :title="$t('Yesterday')" id="aYesterday" typ="Yesterday" class="Yesterday"
                                            @click="ClickTimer('Yesterday', this, pgaction);">{{ $t('Yesterday') }}</a><i
                                            id="pin2" class="fa fa-thumb-tack mt-1 inpinner pinhide" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                    <li id="3"><a :title="$t('ThisWeek')" id="aWeek" typ="Week" class="Week"
                                            @click="ClickTimer('Week', this, pgaction);">{{ $t('ThisWeek') }}</a><i
                                            id="pin3" class="fa fa-thumb-tack mt-1 pinhide inpinner" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                    <li id="4"><a :title="$t('AllTime')" id="aAll" typ="ThisYear" class="AllTime"
                                            @click="ClickTimer('AllTime', this, pgaction);">{{ $t('ThisYear') }}</a><i
                                            id="pin4" class="fa fa-thumb-tack mt-1 pinhide inpinner" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                </ul>
                            </td>
                            <td valign="top" class="daterangetd">
                                <ul class="date_select">
                                    <li id="5"><a id="aLastWeek" class="LastWeek" typ="LastWeek" :title="$t('LastWeek')"
                                            @click="ClickTimer('LastWeek', this, pgaction);">{{ $t('LastWeek') }}</a><i
                                            id="pin5" class="fa fa-thumb-tack mt-1 pinhide inpinner" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                    <li id="6"><a id="aMonth" class="Month" typ="Month" :title="$t('ThisMonth')"
                                            @click="ClickTimer('Month', this, pgaction);">{{ $t('ThisMonth') }}</a><i
                                            id="pin6" class="fa fa-thumb-tack mt-1 pinhide inpinner" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                    <li id="7"><a id="aLastMonth" class="LastMonth" typ="LastMonth" :title="$t('LastMonth')"
                                            @click="ClickTimer('LastMonth', this, pgaction);">{{ $t('LastMonth') }}</a><i
                                            id="pin7" class="fa fa-thumb-tack mt-1 pinhide inpinner" v-show="showPin"
                                            aria-hidden="true"></i></li>
                                </ul>
                            </td>
                        </tr>


                        <tr class="float-left p-0 m-0 mt-1 col-12 daterangetr">
                            <td class="col-4 m-1 daterangetd  float-left table_datepickerfix">
                                <label>From ...</label><br />
                                <v-date-picker v-model="fromDate" mode="date" @input="onDateChange($event, 'fromdate')">
                                    <template v-slot="{ inputValue, inputEvents }">
                                        <input :id="'txtFromDate_' + pgaction" name="startDate"
                                            class="textfield2 ie_crossicon_none FromDate form-control p-1"
                                            :value="inputValue" v-on="inputEvents" />
                                    </template>
                                </v-date-picker>
                                <input type="hidden" :id="'txthiddenFromDt_' + pgaction" value="">
                            </td>
                            <td class="col-4 m-1 daterangetd  float-left table_datepickerfix">
                                <label>...To</label><br />
                                <v-date-picker v-model="toDate" mode="date" :min-date="minToDate"
                                    @input="onDateChange($event, 'todate')">
                                    <template v-slot="{ inputValue, inputEvents }">
                                        <input :id="'txtToDate_' + pgaction" name="endDate"
                                            class="textfield2 ie_crossicon_none ToDate form-control p-1" :value="inputValue"
                                            v-on="inputEvents" />
                                    </template>
                                </v-date-picker>

                                <input type="hidden" :id="'txthiddenToDt_' + pgaction" value="">

                            </td>
                            <td class="col-2 m-1 ml-2 daterangetd float-left table_datepickerfix">
                                <input type="button" :id='"btnsearch_" + pgaction'
                                    class="btn btn-success float-left p-1 p-1 pl-4 pr-4" data-result="btnsearch" value="Go"
                                    @click="searchGoclick(this, pgaction);" />
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <input type="hidden" id="hdnDateSpan" value="Month"
                title="hidden ctr for keeping current Search type Like week, month and day." />
            <input type="hidden" id="ReportViewer" name="returnType" />
            <input type="hidden" id="pgaction" name="pgaction" :value='pgaction' />
            <input type="hidden" id="hdnViewId" name="viewid" />
            <input type="hidden" id="hdnPinnedViewId" name="viewid" />
        </div>
    </div>
</template>

<script>
import DataService from '../../../service/DataService';
export default {
    props: {
        pgaction: null
    },
    data() {
        return {
            isLoading: false,
            fromDate: null,
            toDate: null,
            minFormDate: null,
            minToDate: null,
            showpopup: false,
            eval: '',
            evaltype: '',
            showPin: false,
            PieWidgetDataSingletData: [],
            FilterSpan: 'Today',
            GlobalType: ''
        }
    },
    created() {
        //this.getPinnedView();
    },
    methods: {

        convertSlashtohypenDateFormat(inputDate) {
            const [day, month, year] = inputDate.split("/");
            const jsDate = new Date(`${year}-${month}-${day}`);
            return jsDate.toISOString().split("T")[0];
        },


        ConvertDateFormat(date) {
            var newDate = new Date(date);
            var mnth = ("0" + (newDate.getMonth() + 1)).slice(-2);
            var day = ("0" + newDate.getDate()).slice(-2);
            var datavalue = [newDate.getFullYear(), mnth, day].join("-");
            return datavalue;
        },
        convertAndSetDate(date) {
            let parts = date.split("/").filter(part => part !== "undefined");
            if (parts.length === 3) {
                const [month, day, year] = parts;
                const formattedDate = `${day}/${month}/${year}`;
                return formattedDate;

            } else
                console.log("Invalid date format");
            return null;
        },



        onDateChange(event, type) {
            var vm = this;
            if (event != null) {
                if (type === 'fromdate') {
                    vm.fromDate = new Date(event);
                    vm.fromDate = vm.ConvertDateFormat(vm.fromDate);
                    vm.minToDate = vm.fromDate
                } else if (type === 'todate') {
                    vm.toDate = new Date(event);
                    vm.toDate = vm.ConvertDateFormat(vm.toDate);
                }
            }
            else {
                // 
            }
        },

        OpenDatePopUp(action) {
            // debugger
            var vm = this;
            // $("#pin" + $("#hdnPinnedViewId").val()).css('disply','block');
            console.log(vm.$refs.daterange.querySelectorAll("#pin" + $("#hdnPinnedViewId").val()))
            $("[id^='DivTimer_']").css("display", "none");
            $("[id^='DivTimer_']").each(function () {
                $(this).css("display", "none");
                if ($(this).prop('id') == 'DivTimer_' + action) {
                    $(this).css("display", "block");

                }

                // if ($(this).css('display') == 'block') {
                //     $(this).css("display", "none");
                // }
            })

            if (this.$parent.$refs.widget != undefined) {

                this.fromDate = this.$parent.$refs.widget.fromDate;
                this.toDate = this.$parent.$refs.widget.toDate;

            }

            if (action != 'global') {
                setTimeout(() => {

                    $('#txthiddenFromDt_' + action).val($('#txthiddenFromDt_' + action).val() == '' ? $('#txthiddenFromDt_global').val() : $('#txthiddenFromDt_' + action).val());
                    $('#txthiddenToDt_' + action).val($('#txthiddenToDt_' + action).val() == '' ? $('#txthiddenToDt_global').val() : $('#txthiddenToDt_' + action).val());
                    $('#txtFromDate_' + action).val($('#txthiddenFromDt_' + action).val());
                    $('#txtToDate_' + action).val($('#txthiddenToDt_' + action).val());


                    var fdt = $('#txtFromDate_' + action).val();
                    var tdt = $('#txtToDate_' + action).val()

                    let parts = fdt.split("/").filter(part => part !== "undefined");

                    if (parts.length == 1) {
                        vm.fromDate = vm.ConvertDateFormat(fdt)
                        $('#txtFromDate_' + action).val(fdt);
                        vm.toDate = vm.ConvertDateFormat(tdt);
                        $('#txtToDate_' + action).val(tdt)
                        vm.minToDate = fdt;
                        return;
                    }
                    else if (parts.length === 3) {
                        let fromdate = vm.convertAndSetDate(fdt);
                        let formettedHypenDate = vm.convertSlashtohypenDateFormat(fromdate);
                        $('#txtFromDate_' + action).val(fromdate);
                        vm.minToDate = formettedHypenDate;
                        var todate = vm.convertAndSetDate(tdt);
                        let formettedToDate = vm.convertSlashtohypenDateFormat(todate);
                        $('#txtToDate_' + action).val(todate);
                        return;
                    }

                }, 200);
            }

        },
        searchGoclick(e, action) {
            var vm = this;
            $("#hdnViewId").val("");
            $(".btnPin").hide();
            if (action == 'global') {
                //vm.$emit('set-date', $('#txtFromDate_' + action).val(), $('#txtToDate_' + action).val());
                vm.ClickTimer(null, null, action)
            }
            else {
                //vm.$parent.$parent.SetDateRange($('#txtFromDate_' + action).val(), $('#txtToDate_' + action).val())
                vm.ClickTimer(null, null, action)
            }

        },
        setFromToDate(action) {
            debugger

            var txtFromDt = '#txtFromDate_' + action;
            var txtToDt = '#txtToDate_' + action;
            setTimeout(() => {
                $(txtFromDt).val($('#txthiddenFromDt_global').val());
                $(txtToDt).val($('#txthiddenToDt_global').val());
            }, 200);

        },
        SearchRecord(e, action) {
            //debugger
            var from = action;
            var frmDate = $('#txtFromDate_' + from).val();
            var toDate = $('#txtToDate_' + from).val();
            var searchtype = $('#aDateLable_' + from).html();
            var ctrollerName = $(e).closest('.ticketingDashboardHead').attr("dctrl");
            var actionName = $(e).closest('.ticketingDashboardHead').attr("daction");
            var divContainer = $(e).closest('.ticketingDashboardHead').find(".reportBlock");
            $.ajax({
                url: '/' + ctrollerName + '/' + actionName,
                data: { fromDate: frmDate, toDate: toDate },
                type: "get",
                async: true,
                cache: false,
                success: function (data) {
                    divContainer.empty();
                    divContainer.append(data);
                },
                error: function () {
                }
            });
        },
        ClickTimer(type, e, action) {
            var vm = this;
            vm.GlobalType = type;
            $("#hdnViewId").val($(e).parent().attr("id"));
            if ($("#hdnViewId").val() == $("#hdnPinnedViewId").val()) {
                //$("#idPinView").css("color", "orange");
                $("#idPinView").closest(".btnPin").addClass("pinmyview");
                $("#idPinView").closest(".btnPin").removeClass("unpinmyview");
            }
            else {
                //$("#idPinView").css("color", "white");
                $("#idPinView").closest(".btnPin").addClass("unpinmyview");
                $("#idPinView").closest(".btnPin").removeClass("pinmyview");
            }
            $(".btnPin").show();
            /*alert($("#hdnViewId").val());*/
            if ($(e).closest("div.DatePopUp").attr("data-pageaction") == "global") {
                //debugger
                //vm.FilterSpan=$(e).text();
                $(e).closest('.date_range').find('#aDateLable_global').find('em.text-white').html('').html($(e).text());
                vm.isLoading = true;
                vm.FadeInOut1(action);
                $("#dashbord-graph-widget").find(".ticketingDashboardHead").each(function () {
                    ////debugger;
                    $(this).find(".DatePopUp").find("a[class=" + type + "]").trigger("click");
                    $(this).find(".DatePopUp").hide();
                });
                if (type == "Today") {
                    vm.GetStartEndDates(type, e, action);
                }
                else if (type == "Yesterday") {

                    vm.GetStartEndDates("yesterday", e, action);
                }
                else if (type == "Week") {
                    vm.GetStartEndDates("thisweek", e, action);
                }
                else if (type == "LastWeek") {
                    vm.GetStartEndDates("lastweek", e, action);
                }
                else if (type == "Month") {
                    vm.GetStartEndDates("thismonth", e, action);
                }
                else if (type == "LastMonth") {
                    vm.GetStartEndDates("lastmonth", e, action);
                }
                else if (type == "AllTime") {
                    vm.GetStartEndDates("thisyear", e, action);
                }
                else {
                    vm.GetStartEndDates("", e, action);
                }
                vm.isLoading = false;
            }
            else {
                if (type == "Today") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates(type, e, action);
                }
                else if (type == "Yesterday") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates("yesterday", e, action);
                }
                else if (type == "Week") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("week");
                    vm.GetStartEndDates("thisweek", e, action);
                }
                else if (type == "LastWeek") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("week");
                    vm.GetStartEndDates("lastweek", e, action);
                }
                else if (type == "Month") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("month");
                    vm.GetStartEndDates("thismonth", e, action);
                }
                else if (type == "LastMonth") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("month");
                    vm.GetStartEndDates("lastmonth", e, action);
                }
                else if (type == "AllTime") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates("thisyear", e, action);
                }
                else {
                    vm.GetStartEndDates("", e, action);
                }
                vm.FadeInOut1(action);
            }
            $(e).closest('.date_range').find('#imgNext').show();
            $(e).closest('.date_range').find('#imgPrev').show();
        },
        FadeInOut() {
            $("#DivTimer").toggle();
        },
        FadeInOut1(e) {
            //debugger
            //var nm = $(e).closest('.date_range').find('#pgaction').val();
            $('#DivTimer_' + e).toggle();
            //$("input[id^='DivTimer_']").toggle();
        },
        GetStartEndDates(dateSpan, e, action) {
            //debugger
            var vm = this;
            vm.isLoading = true;
            if (dateSpan != "") {
                var params = `DateSpan=${dateSpan.toLowerCase()}&report=${$('#hdnGroupBy').val()}`;
                DataService.GetStartEndDate_New(params).then(function (response) {
                    vm.isLoading = false;
                    var data = response.data;
                    vm.fromDate = data.dateStart;
                    vm.toDate = data.dateEnd;

                    var fdt = data.dateStart;
                    fdt = vm.ConvertDateFormat(fdt);
                    var tdt = data.dateEnd;
                    tdt = vm.ConvertDateFormat(tdt);

                    $('#txtFromDate_' + action).val(data.dateStart);
                    $('#txtToDate_' + action).val(data.dateEnd);
                    vm.FilterSpan = data.displayText
                    // $('#aDateLable_' + action).find('em').text(data.displayText);
                    vm.$emit('set-date', data.dateStart, data.dateEnd);
                    if ('action' != 'global') {
                        setTimeout(() => {

                            vm.FetchDataSingle(action, fdt, tdt);
                        }, 500);
                    }
                    // else{
                    //     debugger
                    //         setTimeout(() => {
                    //             vm.changeInLocalReport(action,fdt,tdt,dateSpan);
                    //         }, 700);

                    // }


                });

            } else {
                var date = new Date();
                var firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
                var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);
                var firstDate = vm.$options.filters.formatDate(firstDay)
                var lastDate = vm.$options.filters.formatDate(lastDay)
                $('#txthiddenFromDt_global').val(firstDate);
                $('#txthiddenToDt_global').val(lastDate);

                $('#txthiddenFromDt_' + action).val($('#txtFromDate_' + action).val() == '' ? firstDate : $('#txtFromDate_' + action).val());
                $('#txthiddenToDt_' + action).val($('#txtToDate_' + action).val() == '' ? lastDate : $('#txtToDate_' + action).val());


                // var fdt = $('#txtFromDate_' + action).val() == '' ? firstDate : $('#txtFromDate_' + action).val();
                // var tdt = $('#txtToDate_' + action).val() == '' ? lastDate : $('#txtToDate_' + action).val();

                var fdt = vm.fromDate;
                fdt = vm.ConvertDateFormat(fdt);
                var tdt = vm.toDate;
                tdt = vm.ConvertDateFormat(tdt);

                $('#aDateLable_' + action).find('em').text(vm.$t('Custom'));
                vm.$emit('set-date', fdt, tdt);
                setTimeout(() => {

                    vm.FetchDataSingle(action, fdt, tdt);
                }, 500);

            }

        },
        FetchDataSingle: async function (action, f_Date, t_Date) {
            debugger
            var vueObj = this;
            if (vueObj.GlobalType == null) {
                vueObj.GlobalType = 'Custom';
                if (action == "global") {
                    $('#aDateLable_global').find('em.text-white').html('').html(vueObj.$t(vueObj.GlobalType));
                }
            }

            if (action != "global") {
                $('#aDateLable_' + action).find('em.text-white').html('').html(vueObj.$t(vueObj.GlobalType));
                var params = `moduleName=${vueObj.moduleName}&reportType=${action}&fromDate=${f_Date}&toDate=${t_Date}&usersDash=`;
                await DataService.GetDashboardData(params).then(function (response) {
                    if (response.data != null && response.data != "") {
                        vueObj.PieWidgetDataSingletData = response.data;
                        if (response.data.status == "Success") {
                            if (response.data.result != null) {
                                vueObj.PieWidgetDataSingletData = response.data.result;

                                vueObj.$parent.$children.forEach(function (item, index) {
                                    if (vueObj.$parent.$children[index].widgetInfo != undefined) {
                                        if (vueObj.$parent.$children[index].widgetInfo.action == action) {
                                            vueObj.$parent.$children[index].PieWidgetData = vueObj.PieWidgetDataSingletData;
                                            vueObj.$parent.$children[index].bindWidget(vueObj.$parent.$children[index], 200, 200);
                                        }
                                    }
                                });

                            }
                        }
                    }
                });

            }
            else {
                $('#aDateLable_global').find('em.text-white').html('').html(vueObj.$t(vueObj.GlobalType));
                // setTimeout(() => {
                //     vueObj.$parent.widgets.forEach(function (item, index) {
                //     $('#txthiddenFromDt_global').val(f_Date);
                //     $('#txthiddenToDt_global').val(t_Date);
                //     $('#aDateLable_' + item.action).find('em.text-white').html('').html(vueObj.$t(vueObj.GlobalType));
                // }); 
                // }, 300);

                vueObj.$parent.widgets.forEach(function (item, index) {
                    $('#txthiddenFromDt_global').val(f_Date);
                    $('#txthiddenToDt_global').val(t_Date);

                    setTimeout(() => {
                        $('#aDateLable_' + item.action).find('em.text-white').html('').html(vueObj.$t(vueObj.GlobalType));
                    }, 500);

                });

            }
        },
        setSingleReport(action) {
            // debugger
            var vm = this;
            if (action != 'global') {
                //debugger
                //vm.$parent.$children['1'].FetchData();
                vm.$parent.$children.forEach(function (item, index) {
                    if (vm.$parent.$children[index].widgetInfo != undefined) {
                        if (vm.$parent.$children[index].widgetInfo.action == action) {
                            vm.$parent.$children[index].FetchDataSingle(action, vm.fromDate, vm.toDate);

                            let ab = [];
                            ab = vm.$parent.$children[index].PieWidgetDataSingletData
                            vm.$parent.$children[index].PieWidgetData = ab;

                            vm.$parent.$children[index].bindWidget(vm.$parent.$children[index], 200, 200);
                        }
                    }
                });

            }
        },
        getPinnedView: function () {
            debugger
            $(".pinhide").hide();
            var vm = this;
            var type = "Project_Dashboard";
            DataService.GetPinnedView('type=' + type).then(function (response) {

                if (response.data.status == "Success") {
                    if (response.data.result.data != null && response.data.result.data.length > 0) {
                        if (response.data.result.data[0].TYPE_ID > 0) {
                            $("#hdnPinnedViewId").val(response.data.result.data[0].TYPE_ID)
                            vm.eval = $("#" + $("#hdnPinnedViewId").val()).find("a").attr("id");
                            vm.evaltype = $("#" + $("#hdnPinnedViewId").val()).find("a").attr("typ");
                            $('.DateLable').find('em.text-white').html($("#" + vm.eval).text());


                        }
                        else {
                            $("#hdnPinnedViewId").val("3");
                        }
                    }
                }
                vm.ClickTimer('Week', this, 'global');
            });
        }
    },
}



$("body").click(function (event) {
    //debugger
    if (event.target.id.indexOf('txtFromDate_') != -1 || event.target.id.indexOf('txtToDate_') != -1)
        return;
    if ($(event.target.parentElement).prop('id') != '') {
        if (
            document.getElementById($(event.target.parentElement).prop('id')).isSameNode(event.target)
            || document.getElementById($(event.target.parentElement).prop('id')).contains(event.target)
        ) {
            var action = $(event.target.parentElement).prop('id').split('_');
            if ($("#DivTimer_" + action[1]).css("display") == 'none') {
                $("#DivTimer_" + action[1]).css("display", "block");
            }


            return;
        }
        else {
            $("[id^='DivTimer_']").css("display", "none");
        }
    }
    else {
        $("[id^='DivTimer_']").css("display", "none");
    }
});


</script>
<style >
.DatePopUp {
    width: 320px;
}

.divtimer_design table.AssetDatePopUp {
    margin-bottom: 0;
}

.divtimer_design table.AssetDatePopUp td.daterangetd {
    padding: 0 !important;
    text-align: left;
    vertical-align: top;
    font-size: 12px;
}

.divtimer_design table.AssetDatePopUp td.sep {
    border-right: 1px solid #D2D2D2;
}

.reporthead .divtimer_design table.AssetDatePopUp td.daterangetd {
    line-height: 30px;
}

.date_range_bi div.divtimer_design span {
    margin-top: 0 !important;
    color: #000;
}

.divtimer_design .bootstrap-datetimepicker-widget {
    position: relative !important;
    top: auto !important;
    left: 5px !important;
}

.AssetDatePopUp tr td.daterangetd.date-range-column {
    max-width: 114px !important;
    display: table-column !important;
    width: auto !important;
}

.AssetDatePopUp tbody.daterangebody tr.daterangetr {
    display: table-row !important;
}



.divtimer_design table.AssetDatePopUp {
    border: none;
    overflow: visible;
}

.divtimer_design table.AssetDatePopUp span {
    color: #333;
}

.vc-popover-content-wrapper.is-interactive {
    z-index: 999;
}

.graph-widget {
    overflow: visible;
}

.gwb_control .graph-widget-heading .vc-day-content {
