<template>
    <!--Custom Loader component start-->

    <!--Custom Loader component start-->
    <transition name="modal">
        <div class="modal d-block my-popups dialog">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title">Reports</h5>
                        </slot>
                        <button v-on:click="$emit('close')" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>
                    </div>
                    <div class="modal-body">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2 mb-3 p-0">
                            <div class="heading-border">
                                <h5 class="h5">
                                    <span>{{ $t('DashboardReportsPopupHeading') }}</span>
                                </h5>
                            </div>
                        </div>
                        <div class="listing mt-2">
                            <div class="table-responsive">
                                <table id="reportsTable" class="table table-bordered dt-responsive nowrap mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th style="width:50px !important;" class="text-center">
                                                <input id="chkAll" class="chkitems checkAllClass" value="" type="checkbox"
                                                    v-on:change="onChange()">
                                            </th>
                                            <th>{{ $t('Report') }}</th>
                                            <th>{{ $t('Description') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-if="widgets.length == 0">
                                            <td colspan="3" class="text-center text-middle text-danger no-record">
                                                {{ $t('NoRecordfound') }}
                                            </td>
                                        </tr>
                                        <tr v-for="item in widgets" :key="item">
                                            <td class="text-center">
                                                <input v-if="item.isselected == 1" class="chkItems" checked="checked"
                                                    data-change-id="no" type="checkbox" :id="item.reporT_ID"
                                                    :data-moduleName="item.controller" />
                                                <input v-else class="chkItems" type="checkbox" data-change-id="no"
                                                    :id="item.reporT_ID" />
                                            </td>
                                            <td>{{ GetReportName(item) }}</td>
                                            <td>{{ item.reporT_DESCRIPTION }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    <div aria-required="false" class="modal-footer" style="flex-wrap: inherit;">
                        <button type="button" title="Save" class="popupgreenbtn btn btn-success"
                            data-toggle-tooltip="tooltip" data-original-title="Save"
                            @click="SaveReportPreferences">Save</button>
                        <button type="button" title="Close" class="popupredbtn btn btn-danger" data-toggle-tooltip="tooltip"
                            data-original-title="Close" v-on:click="$emit('close')">Close</button>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import DataService from '../../../service/DataService'
export default {
    props: {
        moduleName: null
    },
    data() {
        return {
            isLoading: false,
            widgets: []
        }
    },
    created() {
        this.OnLoadReports();
    },
    mounted() {
        var vm = this;
        setTimeout(function () {
            vm.CheckBoxBootstrap();
            vm.ResponsiveDataTable('reportsTable');
            vm.checkAll();
        }, 500);
    },
    methods: {
        OnLoadReports() {
            var vm = this;
            vm.isLoading = true;
            DataService.GetReports('moduleName=' + vm.moduleName)
                .then(function (response) {
                    vm.isLoading = false;
                    //debugger
                    vm.widgets = response.data.list;

                    $('.checkAllClass').attr("checked", true);
                    vm.widgets.forEach(evt => {
                        if(evt.isselected==0){
                            $('.checkAllClass').attr("checked", false);
                        }
                    })
                })
        },
        onChange() {

        },
        checkAll() {
            $('.checkAllClass').attr("checked", true);
            $('#reportsTable tbody tr input:checkbox').each(function () {
                
                if (this.checked==false) {
                    $('.checkAllClass').attr("checked", false);
                }
            });

        },
        GetReportName(item) {
            var resouceValue = this.$t(item.reporT_HEADING);
            if (!resouceValue) {
                resouceValue = item.reporT_HEADING;
            }
            return resouceValue;
        },
        SaveReportPreferences(reportIds, moduleName) {
            ////debugger;
            var vm = this;
            var selectedReport = $('#divReports input.chkItems:checked');
            if (selectedReport.length < 3) {
                vm.ShowAlert(vm.$t('CustomReportErrorMessage', [vm.$t('Report')]), "failure", true, vm.$t("Alert"));
                return;
            }
            else if (selectedReport.length > 8) {
                vm.ShowAlert(vm.$t('DashboardReportsMaxLimit', [vm.$t('Report')]), "failure", true, vm.$t("Alert"));
                return;
            }
            var reportIds = '';
            for (var i = 0; i < selectedReport.length; i++) {
                reportIds += $(selectedReport[i]).attr('id') + ',';
            }
            reportIds = reportIds.slice(0, -1);
            vm.isLoading = true;
            var param = `reportIds=${reportIds}&moduleName=${vm.moduleName}`;
            DataService.widgetSave(param).then(function (response) {
                vm.isLoading = false;
                if (response.data == 1) {
                    vm.$emit('close');

                } else if (data == 2) {
                    vm.ShowAlert(vm.$t("DashboardReportsMaxLimit"), "failure", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t("InsertUpdateError"), "failure", true, vm.$t("Alert"));
                }
            })
        }
    }
}
</script>